import requests from "./httpService";

const OrderServices = {
  getAllOrders(body) {
    return requests.get("/api/orders", body);
  },

  getOrderByUser(id, body) {
    return requests.get(`/api/orders/user/${id}`, body);
  },

  getOrderById(id, body) {
    return requests.get(`/api/orders/${id}`, body);
  },

  updateOrder(id, body, headers) {
    return requests.put(`/api/orders/${id}`, body);
  },

  deleteOrder(id) {
    return requests.delete(`/api/orders/${id}`);
  },

  // getAllRentalsOrders(body) {
  //   return requests.get('/rentals?admin=1', body);
  // },
};

export default OrderServices;
